.checkbox,
.radio {
  cursor: pointer;
  position: relative;
  -webkit-transition: 0.25s;
  -o-transition: 0.25s;
  transition: 0.25s;
  -webkit-backface-visibility: hidden;
  -moz-backface-visibility: hidden;
  backface-visibility: hidden;
}
.checkbox input,
.radio input {
  outline: none !important;
  opacity: 0;
  filter: alpha(opacity=0);
  zoom: 1;
}
.checkbox.checked .icon,
.radio.checked .icon {
  background-position: -60px -30px;
  opacity: 1;
  display: block \9;
}
.checkbox.checked .icon-to-fade,
.radio.checked .icon-to-fade {
  opacity: 0;
  display: none \9;
  display: none !important;
}
.checkbox.disabled,
.radio.disabled {
  color: #d7dddd;
  cursor: default;
}
.checkbox.disabled .icon,
.radio.disabled .icon {
  opacity: 0;
  display: none \9;
  display: none !important;
}
.checkbox.disabled .icon-to-fade,
.radio.disabled .icon-to-fade {
  background-position: -30px -60px;
  opacity: 1;
  display: block \9;
}
.checkbox.disabled.checked .icon,
.radio.disabled.checked .icon {
  background-position: 0 -90px;
  opacity: 1;
  display: block \9;
}
.checkbox.disabled.checked .icon-to-fade,
.radio.disabled.checked .icon-to-fade {
  opacity: 0;
  display: none \9;
  display: none !important;
}
.checkbox.hover .icon,
.radio.hover .icon {
  background-position: 0 -90px;
  opacity: 1;
  display: block \9;
}
.checkbox.hover .icon-to-fade,
.radio.hover .icon-to-fade {
  opacity: 0;
  display: none \9;
  display: none !important;
}
.checkbox .icon,
.radio .icon,
.checkbox .icon-to-fade,
.radio .icon-to-fade {
  background: url("../../../images/checkbox.png") -90px 0 no-repeat;
  display: block;
  height: 20px;
  left: 0;
  opacity: 1;
  position: absolute;
  top: 0;
  width: 20px;
  -webkit-transition: opacity 0.1s linear;
  -o-transition: opacity 0.1s linear;
  transition: opacity 0.1s linear;
  -webkit-backface-visibility: hidden;
  -moz-backface-visibility: hidden;
  backface-visibility: hidden;
}
.checkbox .icon,
.radio .icon {
  opacity: 0;
  top: 0;
  z-index: 2;
