.toggle {
  background-color: #34495e;
  border-radius: 60px;
  color: white;
  height: 29px;
  /* margin: 0 12px 12px 0; */
  overflow: hidden;
  *zoom: 1;
  display: inline-block;
  zoom: 1;
  *display: inline;
  -webkit-transition: 0.25s;
  -o-transition: 0.25s;
  transition: 0.25s;
  -webkit-backface-visibility: hidden;
  -moz-backface-visibility: hidden;
  backface-visibility: hidden;
  cursor: pointer;
}
.toggle:before,
.toggle:after {
  display: table;
  content: "";
}
.toggle:after {
  clear: both;
}
.toggle.toggle-off {
  background-color: #cbd2d8;
}
.toggle.toggle-off .toggle-radio {
  background: url("../../../images/toggle/icon-off.png") 0 0;
  color: white;
  left: 0;
  margin-left: 0.5px;
  margin-right: -13px;
  z-index: 1;
}
.toggle.toggle-off .toggle-radio:first-child {
  left: -120%;
}
.toggle .toggle-radio {
  cursor: pointer;
  background: url("../../../images/toggle/icon-on.png") right top no-repeat;
  color: #39b2a9;
  display: block;
  font-weight: 700;
  height: 21px;
  left: 120%;
  margin-left: -13px;
  padding: 5px 32px 3px;
  position: relative;
  text-align: center;
  z-index: 2;
  margin-top: 0;
  -webkit-transition: 0.25s;
  -o-transition: 0.25s;
  transition: 0.25s;
  -webkit-backface-visibility: hidden;
  -moz-backface-visibility: hidden;
  backface-visibility: hidden;
}
.toggle .toggle-radio:first-child {
  margin-bottom: -29px;
  left: 0;
}
.toggle input {
  display: none;
  position: absolute;
  outline: none !important;
  display: block \9;
  opacity: 0.01;
  filter: alpha(opacity=1);
  zoom: 1;
}
.toggle label {
  box-sizing: content-box;
  -webkit-box-sizing: content-box;
  line-height: 1.4;
}
.toggle.toggle-icon {
  border-radius: 6px 7px 7px 6px;
}
.toggle.toggle-icon .toggle-radio {
  background: url("../../../images/toggle/block-on.png") 62px 0;
  border-radius: 6px;
  min-width: 27px;
  text-align: right;
}
.toggle.toggle-icon .toggle-radio:first-child {
  text-align: left;
}
.toggle.toggle-icon.toggle-off {
  border-radius: 7px 6px 6px 7px;
}
.toggle.toggle-icon.toggle-off .toggle-radio {
  background: url("../../../images/toggle/block-off.png") 0 0;
}
