{
    position: relative;

    cursor: pointer;
    -webkit-transition: .25s;
         -o-transition: .25s;
            transition: .25s;

    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
}
.checkbox input,
.radio input
{
    zoom: 1;

    opacity: 0;
    outline: none !important;

    filter: alpha(opacity=0);
}
.checkbox.checked .icon,
.radio.checked .icon
{
    display: block \9;

    opacity: 1;
    background-position: -60px -30px;
}
.checkbox.checked .icon-to-fade,
.radio.checked .icon-to-fade
{
    display: none \9;
    display: none !important;

    opacity: 0;
}
.checkbox.disabled,
.radio.disabled
{
    cursor: default;

    color: #d7dddd;
}
.checkbox.disabled .icon,
.radio.disabled .icon
{
    display: none \9;
    display: none !important;

    opacity: 0;
}
.checkbox.disabled .icon-to-fade,
.radio.disabled .icon-to-fade
{
    display: block \9;

    opacity: 1;
    background-position: -30px -60px;
}
.checkbox.disabled.checked .icon,
.radio.disabled.checked .icon
{
    display: block \9;

    opacity: 1;
    background-position: 0 -90px;
}
.checkbox.disabled.checked .icon-to-fade,
.radio.disabled.checked .icon-to-fade
{
    display: none \9;
    display: none !important;

    opacity: 0;
}
.checkbox.hover .icon,
.radio.hover .icon
{
    display: block \9;

    opacity: 1;
    background-position: 0 -90px;
}
.checkbox.hover .icon-to-fade,
.radio.hover .icon-to-fade
{
    display: none \9;
    display: none !important;

    opacity: 0;
}
.checkbox .icon,
.radio .icon,
.checkbox .icon-to-fade,
.radio .icon-to-fade
{
    position: absolute;
    top: 0;
    left: 0;

    display: block;

    width: 20px;
    height: 20px;

    -webkit-transition: opacity .1s linear;
         -o-transition: opacity .1s linear;
            transition: opacity .1s linear;

    opacity: 1;
    background: url('../../../images/checkbox.png') -90px 0 no-repeat;

